/* Reset some default browser styles */
body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

/* Set base font and background styles */
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  background-color: #f4f4f4;
  color: #333;
}

/* Remove list bullets and add padding to unordered lists */
ul {
  list-style: none;
  padding-left: 0;
}

/* Add some spacing to all sections */
section {
  padding: 20px;
  background: #fff;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

/* Add a global style for links */
a {
  color: #3498db;
  text-decoration: none;
}

/* Add a hover effect to links */
a:hover {
  text-decoration: underline;
}

/* Add some basic styling to headers */
header {
  background: #333;
  color: #fff;
  padding: 10px 0;
}

header nav {
  display: flex;
  justify-content: center;
  gap: 20px;
}

header nav a {
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}

header nav a:hover {
  background: #444;
}

main {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}
